@import url('https://fonts.googleapis.com/css2?family=Public+Sans:wght@300;400;700&display=swap');
*{
  margin:0;
  padding:0;
  box-sizing: border-box;
  font-family: "public Sans";
}
body{
  font-size: 18px;
  width:100vw;
  overflow-x: hidden;
}
nav{
  display: flex;
  justify-content: space-between;
  padding:20px 10vw;
  background: white;
  z-index: 9999;
}
nav img{
  height:30px;
  max-width: 150px;
}
#menu{
  width:35px;
  height:25px;
  display: none;
  cursor: pointer;
}
ul{
  padding:10px;
}
li{
  list-style: none;
  padding:0 10px;
  margin:0 10px;
  display: inline;
  cursor: pointer;
  position: relative;
}
nav a{
  text-decoration: none;
  color:hsl(233, 8%, 62%);
}
nav li:hover{
  color:hsl(233, 26%, 24%);
  font-weight: 700;
  
}
li:hover::after{
  content: "";
  position: absolute;
  bottom: -15px;
  left:10px;
  height: 4px;
  width:80%;
  background: hsl(192, 70%, 51%);
}
.btn
{
  padding:10px 20px;
  border-radius:20px;
  color:hsl(0, 0%, 100%);
  background: linear-gradient(to right, hsl(136, 65%, 51%),hsl(192, 70%, 51%) );
width:fit-content;
cursor: pointer;
}
.btn:hover{
  opacity: .6;
}
#home,#about
{
  padding:10%;
  position: relative;
}
#front
{
  display: flex;
  justify-content: space-between;
}
#text
{
  display:flex;
  flex-direction: column;
  /* justify-content: space-between; */
  max-width: 500px;
}

 h1,h2{
  /* margin:10px 0; */
  font-weight: 400;
font-size: clamp(40px,5vw,4rem);
color:hsl(233, 26%, 24%);
}
p{
  margin:30px 0;
  color: hsl(233, 8%, 62%);
}
h2{
font-size: clamp(30px,3.5vw,2.8rem);
}
#image
{
  /* width:40%;
  max-width: 500px; */
  position: absolute;
  top:-20%;
  right:-10%;
  z-index: -1;
  background-image: url(./bg-intro-desktop.svg);
  background-size: 100vw;
  background-repeat: no-repeat;
  background-position: left;
}
#choose
{
  margin-top:100px;
  max-width: 700px;
}
.grid
{
  display: grid;
  grid-template-columns: repeat(4,1fr);
  margin:80px 10%;
}
h3{
  color:hsl(233, 26%, 24%);
  font-size: 1.2rem;
  margin-top:20px;
}
.card
{
  margin-right:30px;
  border-radius: 12px;
  /* padding:0 10px; */
  overflow: hidden;
  max-width: 300px;
}
h5,h6{
  color:hsl(233, 8%, 62%);
  font-weight: 300;
  margin:10px 0;
}
#blog .card p
{
  color:hsl(233, 26%, 24%);
  
}
#blog .card p:hover,.smallcards a:hover{
  color:hsl(192, 70%, 51%);
  cursor: pointer;
}
footer{
  background: hsl(233, 26%, 24%);
  padding:50px 10%;
  display: flex;
  justify-content: space-between;
}
#left
{
  display: flex;
  justify-content: space-between;
  width:700px;
}
#contact
{
  margin-top: 100px;
}
.smallcards li{
  margin-right:10px;
  padding:6px 0;
}
.smallcards a{
  text-decoration: none;
  color: hsl(233, 8%, 62%);
  margin:15px 0;
  display: flex;
  flex-direction: column;
}
#blog img{
  width:100%;
  height:150px;
}
#cr
{
  margin-top: 80px;
}
@media (max-width:1000px){
 
  .grid{
    grid-template-columns: repeat(3,1fr);
  }
  footer
  {
    flex-direction: column;
  }
  ul{
    width:90vw;
    min-width: 350px;
    position: absolute;
    top:-120%;
    left:5%;
    z-index: 500;
    overflow: hidden;
    background-color: white;
  }
  #menu
  {
    display: block;
  }
  nav li{
    display: block;
    margin:20px;
    text-align: center;
  }
  #image 
  {
  position: relative;
  max-width: 100vw;
  }
  #image img
  {
    width:100%;
    margin:10px auto;
    transform: translateY(-25%);
  }
  #right
  {
    margin-top:50px;
  }
  .smallcards
  {
    margin:10px 0;
  }
  #front
  {
    flex-direction: column-reverse;
  }
  nav .btn
  {
  display: none;
  }
  nav a{
    color:hsl(233, 26%, 24%);
  }
  .hide
{
  /* visibility: hidden; */
  display: none;
}
}
@media (max-width:800px)
{
  .grid{
    grid-template-columns: repeat(2,1fr);
  }
  #left
  {
    flex-direction: column;
  }

}
@media (max-width:600px)
{
  .grid 
  {
    grid-template-columns: repeat(1,1fr);
    grid-gap:50px;
  }
  .card{
    margin:0;
  }


}



